import React from "react"
import {
  FaBuilding,
  FaRegMehBlank,
  FaHammer,
  FaWeight,
  FaArrowAltCircleRight,
  FaTrophy,
  FaUser,
  FaUniversity,
  FaTag,
} from "react-icons/fa"
import "../styles/bootstrap.min.css"
import Background from "../images/background.jpg"
import Layout from "../components/layout"
import Head from "../components/Head"
import { Link, graphql } from "gatsby"

export const query = graphql`
  query {
    allContentfulBlogPost(
      sort: { fields: publishedDate, order: DESC }
      limit: 3
    ) {
      edges {
        node {
          publishedDate(formatString: "MMMM Do, YYYY")
          title
          author
          description
          slug
          image {
            file {
              url
            }
          }
        }
      }
    }
    allContentfulUnits(limit: 3) {
      edges {
        node {
          title
          description
          slug
          image {
            title
            file {
              url
            }
          }
        }
      }
    }
    allContentfulStatistics(limit: 1) {
      edges {
        node {
          title
          awards
          lawyers
          cases
          description
        }
      }
    }
    allContentfulWhyChoseUs {
      edges {
        node {
          title
          experience
          trust
          cases
          lawyers
          subTitle
          iconOne
          iconTwo
          iconThree
          iconFour
          needHelp
          needHelpDescription
          image {
            title
            file {
              url
            }
          }
        }
      }
    }
    allContentfulPublications(limit: 2) {
      edges {
        node {
          title
          description
          file {
            file {
              url
              fileName
            }
          }
          image {
            file {
              url
            }
          }
        }
      }
    }
  }
`

const IndexPage = props => {
  const data = props.data.allContentfulBlogPost
  const units = props.data.allContentfulUnits
  const publication = props.data.allContentfulPublications
  const statistic = props.data.allContentfulStatistics
  const whyChoseUs = props.data.allContentfulWhyChoseUs

  return (
    <Layout>
      <Head title="Home" />
      <section className="welcome-section welcome-space">
        <div className="auto-container">
          {whyChoseUs.edges.map(edge => {
            return (
              <div className="row clearfix" key={edge.node.title}>
                <div className="col-md-5 col-sm-12 col-xs-12 sm-text-center">
                  <h1 className="m-top30">{edge.node.title}</h1>
                  <div className="thumb m-top30">
                    <img
                      src={edge.node.image.file.url}
                      alt={edge.node.image.title}
                    />
                  </div>
                </div>
                <div className="col-md-7 col-sm-12 col-xs-12">
                  <div className="row">
                    <div className="col-xs-6 col-sm-3">
                      <div className="icon-style text-center">
                        <i className="icon icon-bg style-round">
                          <FaBuilding />{" "}
                        </i>{" "}
                        <h4
                          style={{
                            boxShadow: `none`,
                            textDecoration: `none`,
                            fontSize: `14px`,
                            color: `black`,
                          }}
                        >
                          {edge.node.iconOne}
                        </h4>
                      </div>
                    </div>
                    <div className="col-xs-6 col-sm-3">
                      <div className="icon-style text-center">
                        <i className="icon icon-bg style-round">
                          <FaRegMehBlank />{" "}
                        </i>{" "}
                        <h4
                          style={{
                            boxShadow: `none`,
                            textDecoration: `none`,
                            fontSize: `14px`,
                            color: `black`,
                          }}
                        >
                          {edge.node.iconTwo}
                        </h4>
                      </div>
                    </div>
                    <div className="col-xs-6 col-sm-3">
                      <div className="icon-style text-center">
                        <i className="icon icon-bg style-round">
                          <FaHammer />{" "}
                        </i>{" "}
                        <h4
                          style={{
                            boxShadow: `none`,
                            textDecoration: `none`,
                            fontSize: `14px`,
                            color: `black`,
                          }}
                        >
                          {edge.node.iconThree}
                        </h4>
                      </div>
                    </div>
                    <div className="col-xs-6 col-sm-3">
                      <div className="icon-style text-center">
                        <i className="icon icon-bg style-round">
                          <FaWeight />{" "}
                        </i>{" "}
                        <h4
                          style={{
                            boxShadow: `none`,
                            textDecoration: `none`,
                            fontSize: `14px`,
                            color: `black`,
                          }}
                        >
                          {edge.node.iconFour}
                        </h4>
                      </div>
                    </div>
                  </div>
                  <h3 className="title-bottom m-top30 m-btm40">
                    {edge.node.subTitle}
                  </h3>
                  <div className="row">
                    <div className="col-sm-6">
                      <div className="feature-box">
                        <h4 className="title">
                          {edge.node.experience}
                          <i className="icon flaticon-banking2 pull-right"></i>
                        </h4>
                        <p className="details">
                          {/* {edge.node.experienceDescription} */}
                        </p>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="feature-box">
                        <h4 className="title">
                          {edge.node.trust}
                          <i className="icon flaticon-hardbound pull-right"></i>
                        </h4>
                        {/* <p className="details">{edge.node.trustDescription}</p> */}
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="feature-box">
                        <h4 className="title">
                          {edge.node.lawyers}
                          <i className="icon flaticon-alarm57 pull-right"></i>
                        </h4>
                        {/* <p className="details">{edge.node.lawyerDescription}</p> */}
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="feature-box">
                        <h4 className="title">
                          {edge.node.cases}
                          <i className="icon flaticon-weighing2 pull-right"></i>
                        </h4>
                        {/* <p className="details">{edge.node.caseDescription}</p> */}
                      </div>
                    </div>
                  </div>
                  <Link
                    to={edge.node.title}
                    style={{
                      boxShadow: `none`,
                      textDecoration: `none`,
                      color: `inherit`,
                    }}
                    className="btn-thm"
                  >
                    Read More
                    <i className="FaArrowAltCircleRight">
                      <FaArrowAltCircleRight />
                    </i>
                  </Link>
                </div>
              </div>
            )
          })}
        </div>
      </section>

      {whyChoseUs.edges.map(edge => {
        return (
          <section
            className="parallax-section"
            key={edge.node.needHelp}
            style={{ backgroundImage: `url(${Background})` }}
          >
            <div className="container">
              <div
                className="text-center wow fadeInUp"
                data-wow-delay="200ms"
                data-wow-duration="1500ms"
              >
                <h2 className="m-btm20">{edge.node.needHelp}</h2>
                <p className="fs-16">{edge.node.needHelpDescription}</p>
                <div className="color-theme fs-18 m-top20">
                  Get an Appointment -{" "}
                  <Link
                    to={`/contact`}
                    className="color-black fw-b text-uppercas"
                  >
                    Contact Us <span className="fa fa-caret-right"></span>
                  </Link>
                </div>
              </div>
            </div>
          </section>
        )
      })}
      {/* { <!--Practic Areas-->} */}
      <section className="Practice-space">
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center">
              <h1>Our Units</h1>
              <p className="color-theme m-btm50"></p>
            </div>
          </div>
          <div className="row">
            {units.edges.map(edge => {
              return (
                <div className="col-sm-6 col-md-4" key={edge.node.title}>
                  <div className="practise-area">
                    <div className="thumb">
                      <img
                        src={edge.node.image.file.url}
                        alt={edge.node.image.title}
                      />
                    </div>
                    <div className="practise-details">
                      <div className="round-style"></div>
                      <i className="icon">
                        <FaUniversity />
                      </i>
                      <h4 className="title">{edge.node.title}</h4>
                      <p className="details">{edge.node.description}</p>
                      <Link
                        to={edge.node.slug}
                        style={{
                          boxShadow: `none`,
                          textDecoration: `none`,
                          color: `inherit`,
                        }}
                        className="btn-thm"
                      >
                        Read More
                        <i className="FaArrowAltCircleRight">
                          <FaArrowAltCircleRight />
                        </i>
                      </Link>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </section>

      <section className="fact-counter fact-space2 theme-overlay">
        <div className="container">
          <div className="row">
            {statistic.edges.map(edge => {
              return (
                <div
                  className="col-md-6 fact-dialogue sm-text-center"
                  key={edge.node.title}
                >
                  <h1 className="our-dialogue">{edge.node.description}</h1>
                  <Link
                    to={`/about`}
                    style={{
                      boxShadow: `none`,
                      textDecoration: `none`,
                      color: `inherit`,
                    }}
                    className="btn-thm btn-white"
                  >
                    Read More
                    <i className="FaArrowAltCircleRight">
                      <FaArrowAltCircleRight />
                    </i>
                  </Link>
                </div>
              )
            })}
            {statistic.edges.map(edge => {
              return (
                <div className="col-md-6" key={edge.node.title}>
                  <div className="row clearfix m-tops40">
                    {/* <!--Column--> */}
                    <article
                      className="col-md-4 col-sm-4 col-xs-12 column wow fadeIn"
                      data-wow-duration="0ms"
                    >
                      <div className="icon-style text-center">
                        <i className="icon icon-bg icon-lg style-round">
                          <FaTrophy />
                        </i>

                        <div className="count-outer">
                          <span className="count-text">{edge.node.awards}</span>
                        </div>
                        <h4
                          className="title"
                          style={{
                            boxShadow: `none`,
                            fontSize: `20px`,
                            textDecoration: `none`,
                            color: `#fff`,
                          }}
                        >
                          Awards
                        </h4>
                      </div>
                    </article>

                    <article
                      className="col-md-4 col-sm-4 col-xs-12 column wow fadeIn"
                      data-wow-duration="0ms"
                    >
                      <div className="icon-style text-center">
                        <i className="icon icon-bg icon-lg style-round">
                          <FaUser />
                        </i>

                        <div className="count-outer">
                          <span
                            className="count-text"
                            data-speed="500"
                            data-stop="547"
                          >
                            {edge.node.lawyers}
                          </span>
                        </div>
                        <h4
                          className="title"
                          style={{
                            boxShadow: `none`,
                            fontSize: `20px`,
                            textDecoration: `none`,
                            color: `#fff`,
                          }}
                        >
                          {" "}
                          Lawyers
                        </h4>
                      </div>
                    </article>

                    <article
                      className="col-md-4 col-sm-4 col-xs-12 column wow fadeIn"
                      data-wow-duration="0ms"
                    >
                      <div className="icon-style text-center">
                        <i className="icon icon-bg icon-lg style-round">
                          <FaUniversity />
                        </i>

                        <div className="count-outer">
                          <span
                            className="count-text"
                            data-speed="1000"
                            data-stop="2145"
                          >
                            {edge.node.cases}
                          </span>
                        </div>
                        <h4
                          className="title"
                          style={{
                            boxShadow: `none`,
                            fontSize: `20px`,
                            textDecoration: `none`,
                            color: `#fff`,
                          }}
                        >
                          Cases
                        </h4>
                      </div>
                    </article>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </section>

      <section className="team-section">
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center mb-5">
              <h1>Publications</h1>
            </div>
          </div>
          <div className="row clearfix">
            {/* <!--Column--> */}
            {publication.edges.map(edge => {
              return (
                <div
                  className="col-md-6 col-sm-6 col-xs-12"
                  key={edge.node.title}
                >
                  <article className="team-area">
                    <div className="row clearfix">
                      <figure className="col-lg-6 col-md-12 image ">
                        <img
                          src={edge.node.image.file.url}
                          alt={edge.node.image.title}
                        />
                      </figure>
                      <div className="col-lg-6 col-md-12">
                        <div className="content pb-4 pl-2">
                          <div className="title-box">
                            <h4>{edge.node.title}</h4>
                          </div>
                          <div className="text">
                            <p>{edge.node.description}</p>
                          </div>
                          <p>{edge.node.publishedDate}</p>
                          <a
                            href={`${edge.node.file.file.url}`}
                            style={{
                              boxShadow: `none`,
                              textDecoration: `none`,
                              color: `inherit`,
                            }}
                            className="btn-thm btn-xs"
                            target="_blank"
                            download
                            rel="noopener noreferrer"
                          >
                            Download
                          </a>
                        </div>
                        <div></div>
                      </div>
                    </div>
                  </article>
                </div>
              )
            })}
          </div>
        </div>
      </section>

      {/* <!--Blog Section--> */}
      <section className="blog-section blog-space">
        <div className="auto-container">
          <div className="row">
            <div className="col-md-12 text-center">
              <h1>Latest From Our Blog</h1>
              <p className="color-theme m-btm50"></p>
            </div>
          </div>

          <div className="row clearfix">
            {data.edges.map(edge => {
              return (
                <div
                  className="col-md-4 col-sm-6 col-xs-12 featured-blog-post wow fadeInLeft"
                  data-wow-delay="0ms"
                  data-wow-duration="1500ms"
                  key={edge.node.title}
                >
                  <article className="inner-box hvr-float-shadow">
                    <figure className="image">
                      <img
                        src={edge.node.image.file.url}
                        alt={edge.node.image.title}
                      />
                    </figure>
                    <div className="post-lower">
                      <div className="post-header">
                        <div className="date">
                          <span className="day">{edge.node.publishedDate}</span>
                        </div>
                        <h3 className="title">{edge.node.title}</h3>
                        <ul className="post-info fs-13">
                          <li className="color-theme">
                            {" "}
                            <span>
                              {" "}
                              <FaUser />{" "}
                            </span>
                            {edge.node.author}
                          </li>
                          <li className="color-theme">
                            {" "}
                            <span>
                              <FaTag />
                            </span>
                            Civil Rights
                          </li>
                        </ul>
                      </div>
                      <div className="post-desc">
                        <p>{edge.node.description}</p>
                        <div className="text-right">
                          <Link
                            to={edge.node.slug}
                            style={{
                              boxShadow: `none`,
                              textDecoration: `none`,
                            }}
                            className="color-theme fs-14"
                          >
                            Read more
                          </Link>
                        </div>
                      </div>
                    </div>
                  </article>
                </div>
              )
            })}
          </div>
        </div>
      </section>
    </Layout>
  )
}
export default IndexPage
